<template>
<body>
  <h1>{{ msg }}</h1>
  <h2>Please Login First</h2>
  <ul>
    <router-link id="login" to="/login">Login</router-link>
  </ul>
</body>
</template>

<script>
export default {
  name: "Welcome",
  data() {
    return {
      msg: "Welcome to My Web!"
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body {
  margin: 0px;
  padding: 0;
}
h1,
h2 {
  font-weight: normal;
  text-align: center;
}
ul {
  list-style-type: none;
  padding: 0;
  text-align: center;
}
li {
  display: inline-block;
  margin: 0px;
}
a {
  color: #42b983;
}
</style>



