<template>
  <div class="block1">
    <el-form>
      <el-form-item>
        <h1></h1>
      </el-form-item>

      <el-date-picker
        class="pot"
        v-model="datenumber"
        type="daterange"
        range-separator="~"
        start-placeholder="StartDate"
        end-placeholder="EndDate"
        format="yyyy-MM-dd"
        value-format="yyyy-MM-dd"
        @change="chooseTimeRange"
      ></el-date-picker>

      <el-button class="fis" icon="el-icon-search" type="primary">Search</el-button>
      <el-form-item>
        <h1></h1>
      </el-form-item>
      <el-form-item>
        <ve-line
          :data="chartData"
          :settings="chartSettings"
          :grid="grid"
          :extend="extend"
          :legend="legend"
          :tooltip="tooltip"
          :yAxis="yAxis"
          :xAxis="xAxis"
          width="1500px"
          height="480px"
        ></ve-line>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    cc: 0
    this.chartSettings = {
      stack: { 用户: ["最高气温", "最低气温", "平均气温"] },
      area: true
    };

    this.extend = {
      backgroundColor: "#FFFFFF",
      //x轴文本设置
      // "xAxis.0.axisLabel.rotate": 45, //x轴文本倾斜
      // 'xAxis.0.axisLine.lineStyle.color': "red",//x轴文本改变颜色
      "xAxis.0.axisLabel.color": "#E6A23C", //x轴文本颜色
      "xAxis.0.axisLabel.fontSize": 16, //x轴文本字体大小
      "xAxis.0.axisLabel.fontFamily": "Arial", //x轴文本字体系列
      "xAxis.0.axisLabel.fontWeight": "bold", //x轴文本字体粗细
      "xAxis.0.axisLabel.align": "center", //x轴文本字体位置

      //y轴文本设置
      // 'yAxis.0.axisLine.lineStyle.color': "green",//y轴文本改变颜色
      "yAxis.0.axisLabel.color": "#F56C6C", //y轴文本改变颜色
      "yAxis.0.axisLabel.fontSize": 16 //x轴文本字体大小
    };
    this.yAxis = {
      name: "单位°C"
    };
    this.tooltip = {
      trigger: "axis",
      backgroundColor: "#0D1B42", //鼠标移动到图上面时，显示的背景颜色
      padding: 15, //定义内边距
      formatter: function(params) {
        //自定义函数修改折线图给数据加单位
        // console.log(params);
        var str = ""; //声明一个变量用来存储数据
        str += "<div>" + params[0].name + "</div>"; //显示日期的函数
        for (var i = 0; i < params.length; i++) {
          // this.cc = params[i].data[1]
          // console.log(params[i].data)
          //图显示的数据较多时需要循环数据，这样才可以成功的给多条数据添加单位
          str +=
            '<div style="color:' +
            params[i].color +
            '"><span>' +
            params[i].seriesName +
            "</span> : <span>" +
            (params[i].data[1] ? params[i].data[1] + "°C" : "暂无") +
            "</span></div>";
        }
        return str;
      }
    };
    this.legend = {
      textStyle: {
        //图例文字的样式
        color: "#000000",
        fontSize: 17,
        fontWeight: "bold"
      }
    };

    this.grid = {
      y: 60
    };

    // printStr: "";
    s: ""; //日期字符串
    return {
      datenumber: null,
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      chartData: {
        columns: ["日期", "最高气温", "最低气温", "平均气温"],
        rows: [
          { 日期: "Day 1", 最高气温: 12, 最低气温: 10, 平均气温: 11 },
          { 日期: "Day 2", 最高气温: 35, 最低气温: 32, 平均气温: 34 },
          { 日期: "Day 3", 最高气温: 29, 最低气温: 26, 平均气温: 24 },
          { 日期: "Day 4", 最高气温: 17, 最低气温: 14, 平均气温: 16 },
          { 日期: "Day 5", 最高气温: 37, 最低气温: 34, 平均气温: 35 },
          { 日期: "Day 6", 最高气温: 45, 最低气温: 42, 平均气温: 43 },
          { 日期: "Day 7", 最高气温: 38, 最低气温: 31, 平均气温: 36 }
        ]
      }
    };
  },
  methods: {
    chooseTimeRange(t) {
      this.s = t.join();
      const split = this.s.split(",");

      console.log(split[0]); //打印第一个日期

      //   this.printStr = t;
    }
  }
};
</script>>
 <style>
.pot {
  top: 10px;
  left: 500px;
}
.fis {
  padding: 12px 10px;
  position: relative;
  top: -30px;
  left: 865px;
}
.block1 {
  line-height: 40px;
  width: 300px;
  height: 80px;
  color: white;
  /* position: absolute; */
  /* float: center; */
  /* background-color: #0c2152; */
}
</style>




